<script>
  import { get } from "../utils";
  import { auth } from "../Auth";

  let promise = get("/games", $auth.token);
</script>

<section class="section">
  <div class="container">
    {#await promise}
      <p>...waiting</p>
    {:then games}
      <ul>
        {#each games as { name }}
          <li>{name}</li>
        {/each}
      </ul>
    {:catch error}
      <p style="color: red">{error.message}</p>
    {/await}
  </div>
</section>
