<script>
  import Datepicker from "svelte-calendar";
  import Autocomplete from "../components/Autocomplete.svelte";
  import { auth } from "../Auth";
  import { get, post } from "../utils";

  export let updateList;

  let promises = Promise.all([
    get("/games", $auth.token),
    get("/activities/statuses", $auth.token),
    get("/friends", $auth.token)
  ]);
  let gameId;
  let isReplay = false;
  let statusId;
  let selectedDate;
  let otherPlayerId;
  const dateFormat = "#{l}, #{F} #{j}, #{Y}";

  async function submit() {
    // Clearing the input field makes the value an empty string which breaks validation
    const createdAt = selectedDate
      ? selectedDate.toISOString().slice(0, 10)
      : null;
    const additionalUserIds = otherPlayerId ? [otherPlayerId] : [];
    await post(
      "/activities",
      { gameId, isReplay, statusId, createdAt, additionalUserIds },
      $auth.token
    );

    updateList();
  }
</script>

<h1 class="title">New Activity</h1>

{#await promises}
  <p>...loading</p>
{:then results}
  <form on:submit|preventDefault={submit}>

    <div class="field">
      <label class="label">What game did you play?</label>
      <div class="control">
        <Autocomplete
          items={results[0]}
          labelFieldName="name"
          valueFieldName="id"
          bind:value={gameId} />
      </div>
    </div>

    <div class="field">
      <div class="control">
        <label class="checkbox">
          <input type="checkbox" bind:checked={isReplay} />
          Are you replaying it?
        </label>
      </div>
    </div>

    <div class="field">
      <label class="label">What is the status?</label>
      <div class="control">
        <Autocomplete
          items={results[1]}
          labelFieldName="name"
          valueFieldName="id"
          placeholder="Status"
          bind:value={statusId} />
      </div>
    </div>

    <div class="field">
      <label class="label">Did you play with anyone else?</label>
      <div class="control">
        <div class="select">
          <select bind:value={otherPlayerId}>
            <option value={undefined}>No</option>
            {#each results[2] as friend}
              <option value={friend.id}>{friend.name}</option>
            {/each}
          </select>
        </div>
      </div>
    </div>

    <div class="field">
      <label class="label">What day did this happen?</label>
      <div class="control">
        <Datepicker bind:selected={selectedDate} format={dateFormat} />
      </div>
    </div>

    <div class="field">
      <div class="control">
        <button
          class="button is-link"
          type="submit"
          disabled={!gameId && !statusId}>
          Create
        </button>
      </div>
    </div>
  </form>
{:catch error}
  <p style="color: red">{error.message}</p>
{/await}
