import {
  navigate
} from "svelte-routing";

import {
  API_PATH
} from './config';

export async function post(url, body, token) {
  return ajax(url, {
    method: "POST",
    body: JSON.stringify(body),
    headers: {
      "Content-Type": "application/json"
    }
  }, token);
}

export async function get(url, token) {
  return ajax(url, {
    method: 'GET'
  }, token);
}

async function ajax(url, options, token) {
  if (token) {
    options.headers = {
      ...options.headers,
      Authorization: `bearer ${token}`
    }
  }
  const req = await fetch(API_PATH + url, options);
  if (!req.ok) {
    console.error(req);
    if (req.status === 401) {
      navigate('/logout');
    }
    throw await req.json();
  }
  return req.json();
}